	import simpleLoading from '@/components/loading/dialog-loading.vue'
	import utils from '@/util/util.js'
	import md5 from '@/common/md5.js'
	import { mapState } from 'vuex';
	export default {
		data() {
			return {
				isHidePassword:true,
				phone:"",
				password:"",
				passwords:'',
				Prompt:false,//提示信息
				onclickindex:0,
				phones:'',
				Psdtext:'请输入最小6位最大12位包含(字母或数字)的密码',
				psdIndex:0,
				oldpassword:'',
				heights:'',
				len:0,
				startX:0,
				moveEndX:0,
				valueX:0,
			};
		},
		computed: mapState(['UserInfo']),
		components:{
			simpleLoading
		},
		onLoad(options) {
			uni.getSystemInfo({
				success: function (res) {
					this.heights=res.windowHeight+'px'	
				}
			});
		},
		methods: {
			touchstart(event){
				event.preventDefault();
　　　　			this.startX = event.touches[0].pageX
			},
			touchmove(event){
				event.preventDefault();
		　　　　this.moveEndX = event.touches[0].pageX
				this.valueX= this.moveEndX - this.startX
				if ( this.valueX >= 80 ) {
						uni.navigateBack({
							delta:1,
							animationType: 'pop-in',
							animationDuration: 200
						})	
		　　　　}
			},
			isHidePasswordClick(){
				this.isHidePassword=!this.isHidePassword
			},
			submit(){
				var _this=this
				if(this.psdIndex==2){
					if(this.onclickindex==0){
					// 弹出加载中... 
					this.$refs.simpleLoading.show({
						image:"../../../static/app-plus/tus.png",
						text:'修改中...'
					});
					this.onclickindex==1
					var json={
						old_login_pwd:this.oldpassword,
						new_login_pwd:this.passwords
					}
					console.log(json)
					var psd=md5(this.oldpassword)
					console.log(psd)
					var AesJson=utils.encrypt(JSON.stringify(json),psd)
					var datas=`{method:"ModifyUserLoginPwd",user_id:"${this.UserInfo.userid}",post_params:"${AesJson}"}`
					console.log(datas)
					this.$api.ReagisterPhone(datas).then((res)=>{
						console.log(res)
						setTimeout(()=>{
							if(res.data.result==1){
								_this.$refs.simpleLoading.hide();
								uni.showToast({
									title:res.data.message,
									duration: 1000,
									icon:"none"
								});
								setTimeout(()=>{
									_this.onclickindex=0
									uni.navigateBack({
										delta:1,
										animationType: 'pop-in',
										animationDuration: 200
									})
								},1000)
							}
							else{
								_this.$refs.simpleLoading.hide();
								uni.showToast({
									title:'旧密码错误',
									duration: 1000,
									icon:"none"
								});
								setTimeout(()=>{
									_this.psdIndex=0
									_this.onclickindex=0
									_this.oldpassword=''
									_this.password=''
									_this.passwords=''
								},1000)
							}
						},1000)
						
					})
				}
				return;
				}
				else{
					console.log("no")
				}
			},
			BlurPsd(){
				var reg2 = /^[A-Za-z0-9]{6,12}$/;
				if(!reg2.test(this.password)){
					this.Psdtext='请输入最小6位最大12位包含(字母或数字)的密码'
					this.Prompt=true
					return;
				}
				this.psdIndex++
				this.Prompt=false
			},
			repeatpsd(){
				if(this.psdIndex==1){
					if(this.password!=this.passwords){
						this.passwords=''
						this.Psdtext='俩次密码不一致'
						this.Prompt=true
						return;
					}
					this.psdIndex++
					this.Prompt=false
				}
				return;
			}
		}
	}
